@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&display=swap);
.app {
  position: relative;

  min-height: 100vh;
  background-color: red;
}

.banner {
  padding: 1rem;
	min-height: 300px;

	background: rgb(34, 193, 195);
	background: linear-gradient(
		90deg,
		rgba(34, 193, 195, 1) 0%,
		rgba(253, 187, 45, 1) 100%
	);
}

.banner__logo {
  margin-bottom: 1rem;

	display: flex;
	flex-direction: column;
}

.logo__icon {
	font-size: 40px;
}

.logo__name {
	font-size: 30px;
	font-weight: 900;

	line-height: 0.8;
}

.banner__author {
  font-weight: 700;
}

.devices {
	position: relative;
	top: -200px;

	max-width: 900px;
	margin: 0 auto;

	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: 0.5rem;
	gap: 0.5rem;
}

.devices__title {
	text-align: center;
	padding: 0.5rem;
	margin-bottom: 1rem;
	width: 100%;

	grid-column: 1/4;
}

.device {
  cursor: pointer;
	padding: 1rem;
  min-height: 100px;
	
  color: #ffffff;
  background: #0a0807;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
	border-radius: 6px;
	transition: background-color 0.2s linear;

	display: flex;
	align-items: flex-start;
	grid-gap: 0.5rem;
	gap: 0.5rem;
}

.device.active {
  color: #0a0807;
  background: #ffffff;
}

.device__info {
	display: flex;
	flex-direction: column;
	grid-gap: 0.25rem;
	gap: 0.25rem;
}

.device__name {
	font-weight: 600;
}

.device__status {
	font-size: small;
}

.card-dark:hover {
	background-color: #ad310b;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html {
	scroll-behavior: smooth;
}

body {
	color: #0a0807;
	font-family: 'Open Sans', sans-serif;
}

a {
	text-decoration: none;
	color: #0a080770;
}

.relative {
	position: relative;
}

.d-flex {
	display: flex;
}

.align-items-center {
	align-items: center;
}

.flex-column {
	flex-direction: column;
}

.g-1 {
	grid-gap: 0.5rem;
	gap: 0.5rem;
}

.g-2 {
	grid-gap: 1rem;
	gap: 1rem;
}

